@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.4.1/css/bootstrap-grid.min.css);
@import url(https://fonts.googleapis.com/css?family=Nunito);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body{background-color:#f2f2f2;font-family:'Nunito', serif;-webkit-font-smoothing:antialiased}*{box-sizing:border-box}ul{list-style:none}hr{border-top:1px solid #f2f2f2;width:100%;margin:0.5em 0}h5{margin:0px;font-size:1.4em;font-weight:700}.row{height:60em;margin:0px}.container{padding-left:8em;padding-right:8em;max-width:100%}.card{display:flex;justify-content:center;align-content:center;width:14em;position:relative;margin:1em}.show-more-box{position:absolute;width:inherit;top:12em;display:flex;justify-content:center;align-items:center;flex-direction:column}.action-link{display:flex;justify-content:flex-end;align-items:center;cursor:pointer}.action-link:hover{font-weight:700;color:#f2f2f2}.action-button{cursor:pointer}.more{display:flex;align-content:center}.blue-circle{background-color:#33CEFF;width:8em;height:4em;border-top-left-radius:8em;border-top-right-radius:8em;position:absolute}.twit-icon{width:3em;height:3em;position:absolute;top:-0.5em}.blue-card{background-color:#33CEFF;height:auto;width:inherit;padding:0.2em 1em;display:-ms-flexbox;flex-direction:column;position:absolute;top:2.2em;transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);border-bottom-left-radius:16px;border-bottom-right-radius:16px;overflow:hidden;box-shadow:15px 15px 15px #e1e1e3;color:white;font-size:0.9em;z-index:100}.white-card{position:absolute;top:0px;background-color:#ffffff;height:14em;width:16em;padding:1em;display:-ms-flexbox;flex-direction:column;transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);border-radius:16px;overflow:hidden;box-shadow:15px 15px 27px #e1e1e3;color:grey}.white-card>.profile-section{transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);display:flex;justify-content:space-between}.white-card>.profile-section>.avatar{margin-right:1em;width:3.1em;height:3.1em;border-radius:5px}.white-card>.profile-section>.info{width:100%}.white-card>.profile-section>.info>.name{margin:0px;font-size:1.45em;font-weight:700}.profile-section>.info>.user{margin:0px;font-size:0.9em;font-weight:400;color:#AAB7B8}.twit-section{margin-top:0.5em;-mos-transition:all 1s;transition:all 1s}.twit-section>.twit-text{margin:5px 0px;font-size:1.1em;font-weight:400}.icon-section{transition:all 0.4s cubic-bezier(0.645, 0.045, 0.355, 1);display:flex;flex-direction:row;justify-content:space-between}

*{margin:0;padding:0;box-sizing:border-box}.input-container{position:relative;margin:auto;width:300px;height:100px}.input-container .search{position:absolute;margin:auto;top:0;right:0;bottom:0;left:0;width:3em;height:3em;background:#33CEFF;border-radius:50%;transition:all 1s;z-index:4;box-shadow:0 0 25px 0 rgba(0,0,0,0.4)}.input-container .search:hover{cursor:pointer}.input-container .search::before{content:"";position:absolute;margin:auto;top:16px;right:0;bottom:0;left:15px;width:9px;height:2px;background:white;transform:rotate(45deg);transition:all .5s}.input-container .search::after{content:"";position:absolute;margin:auto;top:-5px;right:0;bottom:0;left:-5px;width:20px;height:20px;border-radius:50%;border:2px solid white;transition:all .5s}.input-container input{position:absolute;margin:auto;top:0;right:0;bottom:0;left:0;width:50px;height:50px;outline:none;border:none;background:white;color:gray;padding:0 80px 0 20px;border-radius:30px;box-shadow:0 0 25px 0 #e1e1e3;transition:all 1s;opacity:0;z-index:5;letter-spacing:0.1em}.input-container input:hover{cursor:pointer}.input-container input:focus{width:300px;opacity:1;cursor:text}.input-container input:focus ~ .search{right:-250px;background:crimson;z-index:6}.input-container input:focus ~ .search::before{top:0;left:0;width:25px}.input-container input:focus ~ .search::after{top:0;left:0;width:25px;height:2px;border:none;background:white;border-radius:0%;transform:rotate(-45deg)}.input-container input::-webkit-input-placeholder{color:gainsboro;opacity:0.5;font-weight:bolder}.input-container input:-ms-input-placeholder{color:gainsboro;opacity:0.5;font-weight:bolder}.input-container input::placeholder{color:gainsboro;opacity:0.5;font-weight:bolder}

